	        transform: translate(-50%, -50%);
}

.slider-content {
	color: white
}

.slider-content:before {
	content: '';
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
	background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, .9)));
	background: linear-gradient(transparent, rgba(0, 0, 0, .9));
	bottom: 0;
	left: 0;

}

